
#mm-prv-read-wdg {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
#mm-prv-read-wdg .alert {
    padding: 12px;
    background-color: #C8E6C9;
    margin-bottom: 30px;
    border-radius: 4px;
}
#mm-prv-read-wdg button {
    border: 1px solid silver;
    padding: 6px;
    font-size: 13px;
    min-width: 100px;
    background-color: #4FC3F7;
    font-weight: bold;
    color: white;
    text-transform: uppercase;
}
#mm-prv-read-wdg label {
    font-size: 12px;
    font-weight: bold;
}
#mm-prv-read-wdg .mm-prv-read-wdg-email-label {
    font-size: 15px;
}
#mm-prv-read-wdg input.mm-prv-read-wdg-email {
    min-width: 200px;
    border: 1px solid silver;
    padding: 6px;
    font-size: 12px;
    border-radius: 2px;
    background: #f8f8f8;
}
#mm-prv-read-wdg .mm-prv-read-wdg-email-container {
    margin-bottom: 10px;
}
#mm-prv-read-wdg .email-sent-text {
    margin-left: 12px;
    font-size: 12px;
    position: relative;
    top: -4px;
}
#mm-prv-read-wdg .mm-prv-read-wdg-request-text {
    margin-bottom: 30px;
}
#mm-prv-read-wdg .mm-prv-read-wdg-request-text label {
      font-size: 14px;
}

.mm-prv-agree-submit-container {
  text-align: left;
  margin-top: 10px;
}

.privacy-paragraphs .paragraph-title {
  font-size: 11px;
  text-align: left;
  padding: 4px;
  font-family: arial;
  font-weight: bold;
}
.privacy-paragraphs .scroll-warning {
  padding: 0px 0 0 22px;
  margin-bottom: 0px;
  text-align: left;
  font-weight: normal;
  color: black;
  font-family: arial;
  font-size: 11px;
  border-radius: 2px;
}

.privacy-req-scroller {
  text-align: left;
  padding: 2px 10px;
  min-height: 50px;
  font-family: arial;
  font-size: 11px;
}
.scroll-area-force-visible-y-bar .ps__scrollbar-y-rail {
  opacity: 1 !important;
  background-color: #eeeeee !important;
  width: 12px;
}
.scroll-area-force-visible-y-bar .ps__scrollbar-y-rail .ps__scrollbar-y {
    max-width: 6px;
}
.scroll-area {
  background: white;
  border: 1px solid silver;
  position: relative;
  margin: auto;
  margin-bottom: 3px !important;
  width: auto;
  min-height: 50px;
  font-family: arial;
  font-size: 11px;
}
